.card__header{
    display: flex;
    color: var(--fill-color);
    padding: 20px 40px;

}
.card__box h1{
    margin-left: 40px;
}
.balance__box{
    background-color: var(--black);
    padding: 30px 40px;
    border-radius: 5px;
    box-shadow: var(--shadow);
    cursor: pointer;
}
.balance__box hr{
    margin: 10px 0;
    border-bottom: 1px solid rgba(167, 167, 167, 0.541);
}

.balance__box h3{
    font-size: 17px;
    font-weight: 500;
}
.balance__box p{
    font-size: 17px;
}

.bal__card{
    display: flex;
    align-items: center;
}
.bal__card{
    display: flex;
    justify-content: space-between;
}
.hide-data{
    display: inline-block;
    background-color: var(--fill-color);
    color: var(--black);
    padding: 3px 5px;
    font-size: 25px;
    border-radius: 100px;
    margin-left: 20px;

}


@media screen and (max-width: 724px){
    .card__box h1 {
        font-size: 25px;
    }
    .balance__box{
        padding: 30px 20px;
    }

    .bal__card h3, .card-info span,  .bal__card p {
        font-size: 15px;
    }
 
}


@media screen and (max-width: 531px ){
    .card__header{
        padding: 10px 20px;
    }
    .card__box h1 {
        font-size: 25px;
        margin-top: 20px;
    }
}