.footer__2{
    display: flex;
    justify-content: space-around;
}
.foot-links{
    display: block;
    text-decoration: none;
    font-size: 14px;
    line-height: 30px;
    color: var(--matte-color);
}

@media screen and (max-width: 676px){
   
.links__page h3{
    font-size: 17px;
}
}
@media screen and (max-width: 618px){
    .foot-links{
        font-size: 12px;
        font-weight: 500;
    }
}

@media screen and (max-width: 534px){
  .footer__2{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 10px;
}
}


