.hero-image img{
    width: 600px;
}
@media screen and (max-width: 1176px){
  .hero-image img{
    width: 500px;
}
}
@media screen and (max-width: 924px){
    .hero-image img{
        width: 400px;
    }
}
@media screen and (max-width: 749px){
    .hero-image img{
        width: 300px;
    }
}

@media screen and (max-width: 577px){
     .hero-image img{
        width: 250px;
    }
}

@media screen and (max-width: 531px){
    .hero-image{
        width: 100%;
    }
     .hero-image img{
        width: 100%;
    }
}









