:root{
   --black: #000;
   --white: #fff;  
  --fill-color: #eee;
  --silver-color: #C0C0C0;
  --matte-color: #28282B;
  --shadow: 3px 2px 3px rgba(0,0,0,.2);
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: 'Poppins', sans-serif;
}
a{
  text-decoration: none;
}
input, button{
   font-family: inherit;
}