.wallet-box h1{
    font-size: 35px;
    width: 100%;
}
.wallet-box p{
    text-transform: uppercase;
    font-weight: 500;
    letter-spacing: 3px;
}
.add-img-box{
    padding: 50px;
}
.add-img-box img {
    width: 500px;
}
@media screen and (max-width: 1068px){
    .wallet-box p{
    margin-left: 40px;
    letter-spacing: 1px;

}
.wallet-box h1{
    font-size: 30px;
    width: 90%;
    margin-left: 40px;
}
.add-img-box img {
        width: 400px;
        padding: 10px;
    }
}


@media screen and (max-width: 816px){
    .wallet-box p{
        font-size: 13px;
    }

    .wallet-box h1{
    font-size: 25px;
}

 .add-img-box img {
        width: 300px;
        padding: 10px;
    }
}

@media screen and (max-width: 672px){
    .wallet-box p{
        font-size: 11px;
        margin-left: 0;
    }

    .wallet-box h1{
    font-size: 20px;
}
    .add-img-box img {
            width: 250px;
            padding: 10px;
        }

}

@media screen and (max-width: 531px){
    .wallet-box p{
        font-size: 14px;
    }
    .wallet-box{
        margin-top: 30px;
    }
    .wallet-box h1{
    font-size: 25px;
    width: 100%;
    margin-left: 0;
}

    .add-img-box{
        width: 100%;
    }
    .add-img-box img {
            width: 100%;
            padding: 0;

        }

}







