.transaction-list{
    display: flex;
    align-items: center;
    padding: 10px 15px;
    background-color: var(--white);
    color: var(--black);
    border-radius: 10px;
    margin: 5px 0;
}
.icon-box{
    background-color: rgb(16, 201, 16);
    border-radius: 100px;
    padding: 2px 6px;
}
.item{
    margin: 0 10px;
}
.done-icon{
    font-weight: 900;
    font-size: 20px;
    color: var(--white);
    transform: translateY(3px);
}

@media screen and (max-width: 937px){
 
.done-icon{
    font-size: 10px;
    transform: translateY(0);
    padding: 0 4px;
   
}
.transaction-list p{
    font-size: 13px;
}

}

@media screen and (max-width: 531px){
    .transaction-list{
        width: 100%;
    }

}



