.btn{
    background-color:var(--black);
    color: var(--white);
    padding: 8px 20px;
    border-radius: 100px;
    cursor: pointer;
    box-shadow: 5px 5px 4px rgba(0,0,0,.2);
    transition: all .4s ease-in-out;
    margin: 10px 0;
    margin-right: 5px;
    border: none;
    font-weight: 500;
    border: 1px solid transparent;
    
}
.btn:hover{
    background-color: var(--white);
    color: var(--black);
    border: 1px solid var(--black);
}

.btn-white{
    background-color: var(--fill-color);
    color: var(--black);
    font-weight: 600;
}

@media screen and (max-width: 856px){
   .btn{
        padding: 10px 14px;
        font-size: 12px;
        font-weight: 500;
    }
}

@media screen and (max-width: 749px){
    .btn{
        padding: 6px 12px;
        font-size: 10px;
    }
}
