.navbar ul li{
    list-style: none;
}
.navbar ul{
    display: flex;
    align-items: center;
}
.nav-link{
    color: var(--black);
    font-weight: 600;
    padding: 10px 20px;
    font-size: 15px;
    border-radius: 100px;
    transition: all .3s ease-in-out;
}
.nav-link:hover{
    background-color: #c0c0c033;
}
@media screen and (max-width: 856px){
    .nav-link{
        padding: 5px 10px;
        font-size: 14px;
    }
}

@media screen and (max-width: 749px){
    .nav-link{
        padding: 5px 5px;
        font-size: 12px;
    }
}


@media screen and (max-width: 531px){
    .nav-link{
       display: none;
    }
}

