.card__settings{
    display: flex;
    align-items: center;
    background-color: var(--white);
    color: var(--black);
    margin: 10px;
    padding: 0 40px;
    border-radius: 10px;
    cursor: pointer;
}
.card__settings span{
    display: flex;
    padding: 10px;
}

@media screen and (max-width: 937px){
  .card__settings span{
    font-size: 13px;
  }


}



