.popup-modal {
    position: fixed;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.63);
    z-index: 1000;
    transition: all .3s ease-in-out;
}

.popup-content{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 40%;
    background-color: var(--white);
    border-radius: 5px;
    padding: 30px;
}
.close-icon{
    text-align: right;
    cursor: pointer;
}

@media screen and (max-width: 894px){
   .popup-content{
    width: 60%;
   }
}


@media screen and (max-width: 636px){
   .popup-content{
    width: 90%;
    top: 40%;
   }
}
