.transaction__box{
    padding: 30px 0;
}
.transaction__details{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 10px;
}

@media screen and (max-width: 937px){
    .transaction__details{
    grid-template-columns: repeat(1, 1fr);
}
.transaction__box h3{
    font-size: 17px;
}
}

@media screen and (max-width: 531px){
    .transaction__details{
    display:block;
}
.transaction__box{
    width: 90%;
}
}

