.form-input input{
    width: 100%;
    height: 43px;
    padding: 10px;
    border: none;
    border: 1px solid #ccc;
    border-radius: 3px;
    border-radius: 100px;
    transition: all .4s ease-in-out;
}
.form-input input:focus{
    outline: none;
    /* box-shadow: 2px 4px 3px blueviolet; */
}
.form-input{
    margin: 10px 0;
}