.features-section{
     background-color: var(--black);
     padding: 20px;
     color: var(--fill-color);
}

.features-box{
    display: flex;
    align-items: center;
    justify-content: space-around;
}


@media screen and (max-width:1089px){
    .features-section{
     padding: 10px;
}
}
@media screen and (max-width: 870px ){
    .feature-list-box h1{
        font-size: 17px;
        margin-left: 8px;
}
}


@media screen and (max-width:531px){
    .features-box{
     flex-direction: column-reverse;
     padding: 20px 0;
}   

.feature-list-box h1{
    font-size: 23px;
    margin-left: 5px;
}
}
