.footer1{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin-bottom: 20px;

}
.app__download-box{
    display: flex;
    align-items: center;
}

.subscribe__box{
    margin: 10px 0;
}

.subscribe__box p{
    margin-left: 5px;
    font-size: 14px;
}

.subscribe__box input{
    width: 280px;
    height: 40px;
    padding: 10px;
    border-radius: 100px;
    font-size: 13px;
}

.footer__socials a {
    display: inline-block;
     color: var(--matte-color);
     background-color: var(--silver-color);
     padding: 7px 11px;
     border-radius: 100px;
     margin: 0 10px;
     transition: all .2s ease-in-out;
}

.footer__socials a:hover{
    background-color: var(--black);
    color: var(--fill-color);
    transform: translateY(-5px);
}

@media screen and (max-width: 676px){
    .footer__socials a{
        margin: 0 3px;
        font-size: 12px;
        padding: 5px 8px;
    }
    .subscribe__box input{
        width: 200px;
        font-size: 12px;
    }

}

@media screen and (max-width: 534px){
    .footer1{
        flex-direction: column;
        align-items: unset;
    }
    .subscribe__box{
        margin: 10px 0;
    }
    .subscribe__box input{
        width: 60%;
        font-size: 11px;
        height: 37px;
    }

    .links__page h3{
        font-size: 15px;
    }
    .footer__socials a {
        padding: 8px 11px;
        font-size: 12px;
        margin: 0 0;
        margin-right: 10px;
    }
}