.mobile__nav-box{
    width: 100%;
    height:  100vh;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.63);
    z-index: 0;
}
.mobile__nav-content{
    position: absolute;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 100vh;
    padding: 30px;
    background-color: var(--white);
}
.close__mobile-nav{
    text-align: right;
}
.mobile__nav-link{
    margin: 10px 0;
}
.mobile_link{
    color: rgb(59, 58, 58);
    font-weight: 500;
    font-size: 14px;
    border-radius: 100px;
    transition: all .3s ease-in-out;
}

@media screen and (max-width: 812px){
    .mobile__nav-content{
        width: 100%;
    }
}
@media screen and (max-width: 462px){
    .mobile__nav-content{
       top: 0;
    }
}


