.hero-title{
    padding: 10px;
}
.hero-title p {
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 4.2px;
}
.hero-title h1 {
    width: 100%;
    font-size: 40px;
}

@media screen and (max-width: 1176px){

    .hero-title h1 {
        width: auto;
        font-size: 35px;
    }
    .hero-title p {
    
        letter-spacing: 1px;
    }

}
@media screen and (max-width: 1110px){
    .hero-title p {
        font-size: 15px;
        letter-spacing: 1px;
    }
      .hero-title h1 {
        width: auto;
        font-size: 30px;
    }
}
@media screen and (max-width: 924px){
      .hero-title h1 {
        width: auto;
        font-size: 25px;
    }
}

@media screen and (max-width: 749px){
    .hero-title p {
        font-size: 12px;
        letter-spacing: 0;
    }
    .hero-title h1 {
        width: auto;
        font-size: 20px;
    }
}


@media screen and (max-width: 577px){
    .hero-title p {
        font-size: 10px;
        letter-spacing: 0;
    }
    .hero-title h1 {
        width: auto;
        font-size: 17px;
    }
}

@media screen and (max-width: 531px){

    .hero-title{
        padding: 30px 0;
    }
     .hero-title p {
        font-size: 13px;
        letter-spacing: 2px;
    }
    .hero-title h1 {
        font-size: 29px;
    }
}
@media screen and (max-width:349px){
    .hero-title p {
        font-size: 10px;
        letter-spacing: 1px;
    }
     .hero-title h1 {
        font-size: 18px;
        letter-spacing: 1px;
    }
}



