.feature-list{
    display: flex;
    align-items: center;
    margin: 10px 0;
}

.feature-list-icon{
     margin-right: 10px;
}

@media screen and (max-width:1089px){
    .feature-list p {
        font-size: 13px;
    }
}

@media screen and (max-width:744px){
    .feature-list p {
        font-size: 12px;
    }
    .feature-list{
        margin: 10px 0;
    }
    .feature-list-icon{
        font-size: 13px;
    }
}




